.Cavalo__header {
  position: relative; }

.Cavalo__header-back {
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  background-color: #9D8855;
  color: #fff;
  padding: 11px 18px;
  text-transform: uppercase;
  font-size: 11px; }

@media screen and (max-width: 576px) {
  .Cavalo__header-back {
    padding: 8px 12px;
    font-size: 9px; } }

.Cavalo__about .nome {
  font-size: 37px;
  margin-bottom: 15px;
  color: #9D8855;
  font-family: 'Lowan'; }

@media screen and (max-width: 992px) {
  .Cavalo__about .nome {
    margin-top: 10px;
    font-size: 36px;
    margin-bottom: 15px; } }

.Cavalo__about .pais {
  position: relative;
  font-weight: bold;
  font-size: 21px;
  padding: 20px 0;
  text-transform: uppercase;
  padding-left: 60px;
  color: #484848;
  margin-bottom: 15px; }

@media screen and (max-width: 992px) {
  .Cavalo__about .pais {
    font-size: 19px; } }

.Cavalo__about .pais:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 44px;
  height: 44px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  background-image: url("../../../assets/images/ico/genealogia.png");
  background-size: contain;
  background-position: center center; }

@media screen and (max-width: 992px) {
  .Cavalo__about .pais:after {
    width: 45px;
    height: 45px; } }

.Cavalo__about .subitem {
  color: #484848;
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 18px; }

@media screen and (max-width: 576px) {
  .Cavalo__about .subitem {
    margin-bottom: 10px; } }

.Cavalo__about .subitem span {
  font-weight: 400;
  margin-left: 10px; }

.Cavalo__informacoes {
  color: #484848;
  font-size: 18px;
  font-weight: lighter;
  padding-right: 30px; }

.Cavalo_resultados {
  background-color: #0A221D;
  padding: 35px;
  font-size: 17px; }

.Cavalo_resultados .item {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #9d8755a1;
  padding: 21px 0; }

.Cavalo_resultados .ano {
  color: #9D8855;
  font-size: 26px;
  font-weight: 500;
  margin-bottom: 7px; }

.Cavalo_resultados .resultado {
  color: #F7F6F1;
  font-weight: 500;
  margin-top: 15px;
  padding-left: 38px; }

.Cavalo_resultados .cavaleiro {
  color: #f7f6f1be;
  position: relative;
  display: block;
  font-weight: lighter;
  padding-left: 38px;
  margin-bottom: 7px; }

.Cavalo_resultados .cavaleiro:after {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: 27px;
  left: 0;
  background-image: url("../../../assets/images/ico/cavaleiro.svg");
  background-position: left center;
  background-size: contain;
  background-repeat: no-repeat; }

.Cavalo_resultados .cavalo {
  position: relative;
  color: #f7f6f1be;
  display: block;
  font-weight: lighter;
  padding-left: 38px; }

.Cavalo_resultados .cavalo:after {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: 27px;
  left: 0;
  background-image: url("../../../assets/images/ico/cavalo.svg");
  background-position: left center;
  background-size: contain;
  background-repeat: no-repeat; }

.Cavalo_resultados .more {
  color: rgba(255, 255, 255, 0.794);
  border: 1px solid #9D8855;
  padding: 9px 15px;
  font-size: 11px;
  text-transform: uppercase;
  margin-top: 15px;
  -webkit-transition: all 0.4s;
  transition: all 0.4s; }

.Cavalo_resultados .more:hover {
  background-color: #9D8855; }
